.scroll {
  display: flex;
  justify-content: center;
  width: 100%;
}

.pokemon {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 5rem;
  max-width: 80rem;
  width: 100%;
}

.list-title {
  margin: 2rem;
  font-size: 3rem;
  -webkit-text-stroke: 1px var(--pokemon-black);
  color: var(--pokemon-red);
  text-align: center;
}

.pokemon-list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 2rem;
  padding: 0;
  margin: 0.2rem 0;
  width: 100%;
}

.pokemon-item {
  min-height: 12rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 5px;
  box-shadow: 2px 2px 2px 2px var(--pokemon-gray);
}

.pokemon-image {
  width: 8rem;
  image-rendering: pixelated;
}

.pokemon-name {
  padding: 0.5rem 0;
  align-self: baseline;
  text-align: center;
  font-size: 1rem;
  width: 100%;
  background: var(--pokemon-dark-red);
  margin-bottom: 0;
  margin-top: auto;
  border-radius: 0 0 5px 5px;
  color: white;
}

.pokemon-item:hover {
  background: var(--pokemon-light-gray);
}

@media (max-width: 900px) {
  .pokemon {
    margin: 0 2rem;
  }

  .list-title {
    margin: 2rem 0;
    font-size: 2rem;
  }

  .pokemon-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
